import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"
import PostIndex2 from "../components/postIndex2/postIndex2";


const Mac = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina B5"
                meta="Toda la información que necesitas sobre la vitamina B5. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B5?"
                description="Toda la información que necesitas sobre la vitamina B5. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B5?"
                lang="es"
            />
            <h1>Vitamina B5</h1>
                <PostIndex2 linksData={[
                        {"url": "#beneficios", "text": "Beneficios de la vitamina B5"},
                        {"url": "#necesidad", "text": "Alimentos"},
                        {"url": "#cantidad", "text": "Cantidad"},
                        {"url": "#deficiencia", "text": "Deficiencia"},
                        {"url": "#suplementos", "text": "Suplementos"},
                        {"url": "#exceso", "text": "Exceso"}
                ]}
                />
                <hr />
                <p class="introductionText">La vitamina B5, tambi&eacute;n llamada &aacute;cido pantot&eacute;nico y pantotenato, es vital para vivir una vida saludable. Como todas las <a className="link" href="/vitaminaB">vitaminas del complejo B</a>, la B5 ayuda al cuerpo a convertir los alimentos en energ&iacute;a. La B5 se encuentra de forma natural en muchas fuentes de alimentos. &quot;Pantot&eacute;nico&quot;, de hecho, significa &quot;de todas partes&quot;, porque la vitamina est&aacute; disponible en muchas fuentes de alimentos.&nbsp;</p>
                <hr />
                <p></p>
            <h2 name={"beneficios"}>Beneficios&nbsp; de la vitamina B5</h2>
            <p>La vitamina B5 proporciona una multitud de beneficios al cuerpo humano. Se encuentra en las c&eacute;lulas vivas como coenzima A (CoA), que es vital para numerosas reacciones qu&iacute;micas, seg&uacute;n un estudio publicado en la revista Vitamins and Hormones.&nbsp;</p>
            <p></p>
            <p></p>
            <p>Las vitaminas B convierten los carbohidratos en glucosa, que es el combustible que produce energ&iacute;a. Seg&uacute;n el Centro M&eacute;dico de la Universidad de Maryland, las vitaminas B tambi&eacute;n ayudan al cuerpo a utilizar la grasa y las prote&iacute;nas y tambi&eacute;n son importantes para mantener el sistema nervioso, los ojos, la piel, el pelo y el h&iacute;gado sanos.</p>
            <p></p>
            <h3>¿Qué hace la vitamina B5? </h3>
            <p></p>
            <ul>
                <li>Crear gl&oacute;bulos rojos&nbsp;</li>
                <li>Crea hormonas sexuales y relacionadas con el estr&eacute;s</li>
                <li>Mantener un tracto digestivo saludable</li>
                <li>Procesar otras vitaminas, en particular la B2 (riboflavina)</li>
                <li>Sintetizar el colesterol</li>
            </ul>
            <p>Tambi&eacute;n se ha descubierto que la vitamina B5, tomada como suplemento, ayuda a reducir el colesterol. En un estudio de 2011 publicado en la revista Nutrition Research, los investigadores del Princeton Longevity Center de Nueva Jersey encontraron que los suplementos de pantetina, un derivado de la vitamina B5, reduc&iacute;an el colesterol total y el colesterol LDL en sujetos con un riesgo cardiovascular de bajo a moderado.&nbsp;</p>
            <p></p>
            <p>Otro estudio realizado en el Colegio M&eacute;dico Asahikawa de Jap&oacute;n descubri&oacute; que la pantetina podr&iacute;a ser beneficiosa para la prevenci&oacute;n de la angiopat&iacute;a diab&eacute;tica. Un estudio de la Academia Nacional de Ciencias de Grodno (Belar&uacute;s) tambi&eacute;n determin&oacute; que la pantetina puede ser &uacute;til en el tratamiento de la diabetes.&nbsp;</p>
            <p></p>
            <p></p>
            <h2 name={"alimentos"}>¿Qué alimentos continen la vitamina B5?</h2>
            <p>Algunas buenas fuentes de &aacute;cido pantot&eacute;nico son los hongos, las legumbres y lentejas, los aguacates, la leche, los huevos, la col, las carnes de &oacute;rganos como el h&iacute;gado y los ri&ntilde;ones, las papas blancas y dulces, los cereales integrales y la levadura, seg&uacute;n la Biblioteca Nacional de Medicina de los Estados Unidos. Adem&aacute;s de estas fuentes, el Instituto Linus Pauling de la Universidad Estatal de Oreg&oacute;n tambi&eacute;n enumera las yemas de huevo, el br&oacute;coli, el pescado, los mariscos, el pollo y el yogur.&nbsp;</p>
            <p></p>
            <p>El Centro M&eacute;dico de la Universidad de Maryland dice que las mejores fuentes de vitamina B5 incluyen la levadura de cerveza, el ma&iacute;z, la coliflor, la col rizada, los tomates, los guisantes partidos, los cacahuetes, la soja, las semillas de girasol, la langosta y el salm&oacute;n.&nbsp;</p>
            <p></p>
            <h2 name={"cantidad"}>&iquest;Cu&aacute;nta B5 necesitas?</h2>
            <p>Hay muy poca informaci&oacute;n sobre las cantidades de B5 permitidas y no existe una cantidad diaria recomendada (RDA) establecida por el Consejo de Alimentaci&oacute;n y Nutrici&oacute;n del Instituto de Medicina. Sin embargo, el consejo ha creado una gu&iacute;a de referencia diet&eacute;tica (DRI) para la B5. Esta es la recomendaci&oacute;n de consumo adecuado de DRI, seg&uacute;n la Biblioteca Nacional de Medicina de los Estados Unidos:</p>
            <p></p>
            <p><b>Edad 0-6 meses: 1,7 miligramos por d&iacute;a</b></p>
            <p><b>Edad 7-12 meses 1.8 mg/d&iacute;a</b></p>
            <p><b>Edad 1-3 a&ntilde;os: 2 mg/d&iacute;a</b></p>
            <p><b>De 4 a 8 a&ntilde;os: 3 mg/d&iacute;a</b></p>
            <p><b>De 9 a 13 a&ntilde;os: 4 mg/d&iacute;a</b></p>
            <p><b>A partir de los 14 a&ntilde;os: 5 mg/d&iacute;a</b></p>
            <p>Las mujeres que est&aacute;n embarazadas o amamantando pueden necesitar cantidades m&aacute;s altas de &aacute;cido pantot&eacute;nico y deben consultar a sus m&eacute;dicos para obtener m&aacute;s informaci&oacute;n.</p>
            <p></p>
            <p></p>
            <h2 name={"deficiencia"}>Deficiencia de vitamina B5</h2>
            <p>Una deficiencia de B5 es muy poco com&uacute;n. De hecho, seg&uacute;n la Universidad Estatal de Oreg&oacute;n, s&oacute;lo se ha encontrado en aquellos con desnutrici&oacute;n severa. El efecto secundario m&aacute;s com&uacute;n de la deficiencia de &aacute;cido pantot&eacute;nico es el malestar generalizado. Los efectos secundarios tambi&eacute;n pueden incluir irritabilidad, insomnio, v&oacute;mitos, depresi&oacute;n, dolores de est&oacute;mago, ardor en los pies e infecciones de las v&iacute;as respiratorias superiores, seg&uacute;n el Centro M&eacute;dico de la Universidad de Maryland. La deficiencia tambi&eacute;n puede causar un crecimiento deficiente, s&iacute;ntomas nerviosos y anemia, aunque es poco com&uacute;n, dijo la Dra. Kristine Arthur, internista del Centro M&eacute;dico Conmemorativo de la Costa de Orange en Fountain Valley, California.</p>
            <p></p>
            <p>No se ha relacionado ninguna enfermedad con una deficiencia de B5 y las c&eacute;lulas no parecen estar afectadas por una deficiencia. Para reponer la falta de &aacute;cido pantot&eacute;nico, las c&eacute;lulas pueden estar equipadas para conservar su contenido de pantotenato, posiblemente reciclando el pantotenato obtenido de otras mol&eacute;culas degradantes, seg&uacute;n un art&iacute;culo de 1991 en Vitaminas y Hormonas.&nbsp;</p>
            <p></p>
            <h2 name={"suplementos"}>Los suplementos de B5</h2>
            <p>Algunos estudios han descubierto que el consumo de suplementos de &aacute;cido pantot&eacute;nico puede ayudar a tratar o prevenir enfermedades. Otro ejemplo, publicado en el International Journal for Vitamin and Nutritional Research, encontr&oacute; que cantidades m&aacute;s altas de pantotenato mejoran la curaci&oacute;n de las heridas. Incluso la toma de un multivitam&iacute;nico que contenga B5 puede ser beneficiosa.&nbsp;</p>
            <p></p>
            <p>&quot;El valor de tomar suplementos es objeto de cierta controversia. Sin embargo, un informe en el Journal of the American Medical Association en 2002 sugiri&oacute; que todos los estadounidenses deber&iacute;an tomar al menos una multivitamina diaria&quot;, dijo a Live Science el Dr. Steve Kushner, fundador y director de I+D de Victory Nutrition International.&nbsp;</p>
            <p></p>
            <p>&quot;Por lo general, siempre es mejor obtener nuestras vitaminas de los alimentos que tomar suplementos cuando podemos&quot;, dijo la Dra. Linda Girgis, m&eacute;dico de familia en South River, Nueva Jersey. &quot;Cuando obtenemos estas vitaminas de los alimentos, se absorben y metabolizan mejor que cuando tomamos suplementos&quot;.</p>
            <p></p>
            <h2 name={"exceso"}>Sobredosis y efectos secundarios</h2>
            <p>Dado que la vitamina B5 es soluble en agua, el exceso es simplemente filtrado por el cuerpo y eliminado por el tracto urinario, hay muy poca preocupaci&oacute;n por una sobredosis. &quot;No se conoce ning&uacute;n nivel t&oacute;xico para la B5&quot;, dijo Arthur.</p>
            <p></p>
            <p>Sin embargo, se ha descubierto que dosis muy altas de vitamina B5, de 10 a 20 gramos por d&iacute;a, causan diarrea, seg&uacute;n la Universidad Estatal de Oreg&oacute;n.&nbsp;</p>
            <p></p>
        </Layout>
    )
}

export default Mac
